import React from 'react';
import {withRouter} from 'react-router';
import ReactPlayer from 'react-player/lazy';
import classNames from 'classnames';

import Koro from '../koro/Koro';

import {KoroTypes} from '../constants';

type Props = {
  className: String,
  subtitle: any,
  video: String,
  title: String,
  background: String,
  koroColor: String,
  koroType: String,
};

const Hero = ({
  className,
  subtitle,
  title,
  background,
  video,
  koroColor = '#ffffff',
  koroType = KoroTypes.BASIC_WAVE,
}: Props) => (
  <div
    className={classNames('myhki-hero', className)}
    style={{background: background || 'transparent'}}
  >
    {!!video && (
      <div className="myhki-hero__video">
        <ReactPlayer
          fileConfig={{attributes: {autoPlay: true, muted: true}}}
          playsinline={true}
          url={video}
          width="100%"
          height="100%"
          loop={true}
          volume={0}
          playing={true}
        />
      </div>
    )}
    <div className="myhki-hero__wrapper">
      {!!subtitle && <div className="myhki-hero__subtitle">{subtitle}</div>}
      {!!title && <h1 className="myhki-hero__title">{title}</h1>}
    </div>
    <Koro type={koroType} color={koroColor} flip />
  </div>
);

export default withRouter(Hero);
